      </b-col>
      <!-- /Left Text-->

      <!-- Login-->
      <b-col
        lg="4"
        class="d-flex align-items-center auth-bg px-2 p-lg-5"
      >
        <!-- Brand logo-->

        <b-link class="brand-logo d-flex justify-content-end w-75">
            <vuexy-logo />
        </b-link>
